<template>
	<div id="pull_req" class="jon-slider">
		<h3 class="text-center mt-5" style="line-height: 1.5em"><b class="text-gradient">Pull Request</b></h3>
		<p class="text-center font-courier text-gold">Kontribusi anda pada project lain</p>
		<div>
			<div v-for="(pr) in pull_req" :key="pr.id" class="commit-item">
				<span class="mr-3">
					<img class="avatar" :src="profil.avatar_url" width="32" height="32">
				</span>
				<div style="width: 100%">
					<div>
						<a href="/" target="_blank"><b>{{pr.actor.login}}</b></a> created pull <a href=""><b>{{pr.repo.name}}</b></a> <small>{{pr.created_at}}</small>
						<div class="commit-detail p-3 mt-2">
							<span>{{pr.payload.size}} commit to</span>
							<a href="" class="gh-branch-name">{{pr.payload.ref}}</a>
							<div class="mt-1">
								<ul style="list-style: none; padding: 0">
									<li style="display: flex;">
										<span>
											<img class="avatar mr-1" :src="profil.avatar_url" width="16" height="16">
										</span>
										<code class="mr-1">{{pr.payload.head.substr(0, 8)}}</code>
										<blockquote style="margin:0"><small>{{(pr.payload.commits[0]) ? pr.payload.commits[0].message : ''}}</small></blockquote>
									</li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div v-if="stat.pull_req > 0" class="section-fade"></div>
		<div class="review-pull-req">
			<div class="row">
				<div class="col-md-6">
					<p class="text-center">Jumlah Pull Request Sampai Saat Ini</p>
					<CounterStyle identify="statpull" :value="stat.pull_req" :key="11"></CounterStyle>
				</div>
				<div class="col-md-6">
					<p class="text-center">Jumlah Pull Request Yang Ingin Anda Capai (2021)</p>
					<CounterStyle identify="resopull" :value="resolution.pull_req" :key="12" :is_res="true"></CounterStyle>
				</div>
			</div>
			<ReviewBox :stat="stat.pull_req" :reso="resolution.pull_req" :progress="progress.pull_req" identify="pull_req" :key="6"></ReviewBox>
		</div>
	</div>
</template>

<script>
import CounterStyle from './../components/CounterStyle'
import ReviewBox from './../components/ReviewBox'
export default {
	data(){
		return {
			// number: []
		}
	},
	components: {
		CounterStyle, ReviewBox
	},
	props: ['stat', 'resolution', 'progress', 'pull_req'],
	methods: {

	},
	mounted(){

	}
}
</script>

